import { createGlobalStyle } from 'styled-components';

const GlobalStyle = createGlobalStyle`   
    html {
        scroll-behavior: smooth;
        font-family: 'Mountserrat';
    }

    body {
        ::-webkit-scrollbar {
            width: 12px;
        }

        ::-webkit-scrollbar-thumb {
            background: #CCCACA;
            border-radius: 20px;

            
        }
    }
`;  

export default GlobalStyle;